import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import '../CSS/Header.css';

export default class Header extends Component {
    render() {
        return (
            <div className="rmdb-header">
              <div className="rmdb-header-component">
                <Link to="/">
                <img src={require('../images/reactMovie_logo.png')} className="rmdb-logo" alt="Logo" />
                </Link>
                <img src={require('../images/tmdb_logo.png')} className="rmdb-tmdb-logo" alt="logo2" />
              </div>
            </div>
        )
    }
}
